* {
  box-sizing: border-box;
}

.section {
  position: relative;
}

.main {
  height: auto;
  width: 100%;
  position: absolute;
}

.main_body {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}
.main_body h3,
.main_body p {
  padding: 0px;
  margin: 0px;
}
.main_body h3 {
  font-size: 15px;
}
.main_body p {
  line-height: unset;
  font-size: 11.5px;
}
.main_body .intro {
  flex-direction: row;
  display: flex;
  align-items: center;
}
.main_body .intro .name_title {
  margin-left: 10px;
}
.main_body img {
  width: 53px;
  border-radius: 8px;
  border: 1px solid #bbb;
}
.main_body img:hover {
  cursor: zoom-out;
}

.intro > div:last-child {
  margin-left: auto;
}
.intro > div:last-child .onsocials {
  display: flex;
  justify-content: space-between;
}
.intro > div:last-child .onsocials a {
  margin: 0px 10px;
}
.intro > div:last-child * {
  color: #04050d;
}

@media only screen and (max-width: 768px) {
  .main_body {
    padding: 20px;
  }
  .main_body h3 {
    font-size: 16px;
  }
  .main_body img {
    width: 55px;
  }
  .intro > div:last-child .onsocials a {
    margin: 0px 0px 0px 16px;
  }
}
.contact {
  height: auto;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: height 1ms;
  transition-timing-function: cubic-bezier(0.17, 0.67, 0.83, 0.61);
}

.contact > p > * {
  font-size: 100px;
}

._contact {
  margin: 68px 0px;
}

._contact > p {
  padding: 100px 0px;
}
._contact > p * {
  font-size: 30px;
  margin: 30px 0px;
}

.open,
.closed {
  width: 100%;
}

.open {
  background: none;
}

.closed {
  height: auto;
  background-color: #1e1e1e;
  color: White;
  transition: 0.3ms ease-in-out;
}

.base_footer {
  height: 24px;
  background-image: url("/src/images/footer.webp");
  background-size: contain;
}

.isEntering {
  transform: rotate(-180deg);
}

.for_the_web {
  padding-top: 50px;
}
.for_the_web h2 {
  font-size: 82px;
  font-weight: 900;
  line-height: unset;
}

.welcome_note article {
  margin-top: 40px;
  max-width: 550px;
}
.welcome_note article p {
  font-size: 15px;
  margin-top: 14px;
  line-height: 24px;
}

@media only screen and (max-width: 768px) {
  .for_the_web {
    padding-top: 10px;
  }
  .for_the_web h2 {
    font-size: 65px;
  }
  .welcome_note article p {
    font-family: 14px;
  }
}
.buttons {
  display: flex;
  align-items: center;
  max-width: 500px;
}
.buttons button:first-child {
  margin-right: 10px;
}
.buttons button {
  font-size: 15px;
  font-weight: 600;
  padding: 8px;
  background-color: transparent;
  border: 1.5px solid #04050d;
  outline: none;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .buttons button {
    margin-bottom: 25px;
  }
}
.project_pointer {
  position: absolute;
  bottom: 150px;
  right: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  transform: rotate(-90deg);
}
.project_pointer * {
  color: #04050d;
}
.project_pointer p {
  margin-left: 25px;
  font-size: 15px;
}

.contact_body,
.main_contact_body {
  max-width: 800px;
  margin: 0px auto;
  padding: 20px;
}

.contact_body,
