@media only screen and (max-width: 768px) {
  .contact_body,
  .main_contact_body {
    padding: 20px;
  }
  .contact_body h3,
  .main_contact_body h3 {
    font-size: 16px;
  }
  .contact_body img {
    width: 55px;
  }
  .intro > div:last-child .onsocials a {
    margin: 0px 0px 0px 16px;
  }
}
.main_contact_body {
  padding: 0px 10px;
}

.projects_rack {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 30px 0px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.project_item {
  flex: 1 1 300px;
  height: 400px;
  border: 1px solid #eee;
  border-radius: 6px;
  padding: 25px;
  cursor: pointer;
  margin: 0px;
  box-sizing: unset;
}

.project_image {
  margin: -25px;
  margin-bottom: 25px;
  border-radius: 6px 6px 0px 0px;
}
.project_image img {
  width: 100%;
  border-radius: 4px 4px 0px 0px;
}

.other_items .project_item_title {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
.other_items .project_item_title .stack-icon {
  display: flex;
  align-items: center;
  font-size: 14.5px;
}
.other_items .project_item_title .stack-icon *:nth-child(1) {
  margin-right: 5px;
}
.other_items .project_item_title h3 {
  font-size: 18px;
}

.react_color * {
  color: #61dafb;
}

.laravel_color * {
  color: #ff2d20;
  font-weight: 700;
}

.other_items {
  position: relative;
}
.other_items p {
  font-size: 13.5px;
  line-height: 24px;
}

.other_items a {
  color: #868f97;
  font-size: 13.5px;
  position: absolute;
  bottom: 30px;
  display: flex;
  align-items: center;
}
.other_items a * {
  margin-left: 10px;
}

.next-project p {
  text-transform: uppercase;
  color: #868f97;
  letter-spacing: 0.23em;
  font-weight: 600;
  margin: 35px 0px 20px;
  line-height: 24px;
}
.next-project div h3 {
  font-size: 39.5px;
  line-height: 28px;
  margin: 10px 0px 20px;
}
.next-project div h3 * {
  color: #eee;
  margin: 0px 0px -10px;
  line-height: 28px;
  display: block;
}
.next-project div h3:nth-child(2) {
  display: flex;
}

.main_contact_body > h2,
.main_contact_body > p {
  text-align: center;
}

@media screen and (max-width: 768px) {
  .next-project p {
    margin: 25px 0px 20px;
  }
  .project_item {
    margin: 10px 0px;
  }
  .next-project div h3 {
    font-size: 36px;
  }
  .next-project div h3 * {
    font-size: 36px;
  }
}
.contact-bib {
  position: absolute;
  bottom: 30px;
  margin-top: 100px;
}
.contact-bib a {
  margin-right: 15px;
}
.contact-bib a > * {
  color: #868f97;
  transition: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.contact-bib a > *:hover {
  color: #eee;
}

.stacks-cage {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 40px 0px;
}

.stack-skills {
  flex: 1 1 180px;
  height: 125px;
  text-align: center;
  margin: 10px 0px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  border-radius: 6px;
}
.stack-skills big * {
  font-size: 46px;
  margin: 10px 0px;
}
.stack-skills p {
  font-size: 14px;
  font-weight: 600;
}
.stack-skills small {
  font-size: 11px;
  text-transform: uppercase;
}

.stack-skills:hover {
  background-color: #868f97;
}
.stack-skills:hover * {
  color: #04050d;
}

@media only screen and (max-width: 768px) {
  .stack-skills {
    flex: 1 1 90px;
  }
}
.scroll_intro {
  display: none;
  width: 100%;
  height: auto;
  background-color: #252526;
  box-shadow: 2px 2px 15px 0 rgba(37, 37, 38, 0.336);
  padding: 5px 0px;
}
.scroll_intro .small_header_content {
  max-width: 800px;
  margin: 0 auto;
  padding: 0px 10px;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
}

.holder_intro {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
}
.holder_intro .search_fie {
  width: 58%;
}
.holder_intro input {
  width: 100%;
  margin: 1px auto;
  font-size: 14px;
  padding: 8px;
  max-width: 800px;
}
.holder_intro input::-moz-placeholder {
  font-size: 12px;
}
.holder_intro input::placeholder {
  font-size: 12px;
}

.search_fie {
  width: 100%;
}

input {
  width: 100%;
  max-width: 800px;
  font-size: 17.5px;
  margin: 30px auto;
  display: block;
